
/* Mobile & Tablet */
@media screen and (max-width : 1023px) {
    .App {
        max-width: 100vw;
        justify-content: center;
        align-items: center;
        margin: 0 auto 35vw;
        iframe{
             width:100vw;
                height:50vw
        }
           .tieuDe{
            width: 98vw;
            font-size: 5vw;
            font-weight: 600;
            text-align: center;
            margin: 3vw auto;
            color: red;
   
           }
           .gioithieuhoian{
            //    display: flex;
               gap: 5vw;
               .anh{
                   width: 100%;
                   height: 60vw;
                   object-fit: cover;
   
               }
               .thongTin{
                //    width: 95%;
                margin: auto 3vw;
                   font-size: 16px;
                //    justify-content: center;
                //    align-items: center;
                   .tieuDe2{
                    font-size: 18px;
                    font-weight: 600;
                    margin-bottom: 25px;
                    text-align: center;

                   }
                   .noiDung{
                    word-wrap:break-word;
                overflow:auto;
                    line-height: 22px;

                   }
                   .lienHe{
                    font-size: 18px;
                    font-weight: 600;
                    margin: 25px auto 10px;
                    text-align: center;

                   }
                   .hotLine{
                    text-align: center;
                    position: relative;

                   }
                   .zalo{
                    width: 100px;
                    height: 100px;
                    border-radius: 50% ;
                    margin: auto;
                    border: solid 1px gray;
                   position: fixed;
               cursor: pointer;
                   right: 30px;
                   bottom: 30px;
                   background: rgba(0, 0, 0, 0.8);
                   z-index: 1;
                //    opacity: 0.6;
                   }
   
               }
           }
           
         }
}

/* PC*/
@media screen and (min-width : 1024px) {
    .App {
        max-width: 1280px;
        justify-content: center;
        align-items: center;
        margin:0 auto 300px;
        iframe{
            width:1280px;
               height:320px
       }
           .tieuDe{
            font-size: 30px;
            font-weight: 700;
            text-align: center;
            margin: 25px auto;
            color: red;
   
           }
           .gioithieuhoian{
               display: flex;
               gap: 5vw;
               .anh{
                   width: 45%;
                   height: 350px;
   
               }
               .thongTin{
                   width: 45%;
                   font-size: 16px;
                   justify-content: center;
                   align-items: center;
                   .tieuDe2{
                    font-size: 18px;
                    font-weight: 600;
                    margin-bottom: 25px;
                    text-align: center;

                   }
                   .noiDung{
                    line-height: 22px;

                   }
                   .lienHe{
                    font-size: 18px;
                    font-weight: 600;
                    margin: 25px auto 10px;
                    text-align: center;

                   }
                   .hotLine{
                    text-align: center;
                    position: relative;

                   }
                   .zalo{
                    width: 100px;
                    height: 100px;
                    border-radius: 50% ;
                    border: solid 1px gray;
                   position: fixed;
               cursor: pointer;
                   right: 30px;
                   bottom: 30px;
                   background: rgba(0, 0, 0, 0.8);
                   z-index: 1;
                   }
   
               }
           }
           
         }
}